import React, { useState } from "react";
import JDialog from "../../components/zent/dialog";
import JDrawer from "../../components/zent/drawer";
import { JLoading } from "../../components/zent/loading";

import {
  LayoutRow as Row,
  LayoutCol as Col,
  LayoutGrid as Grid,
  LayoutConfigProvider as ConfigProvider,
} from "zent";

import { Alert, AnimateHeight, Button, Placeholder, Sweetalert } from "zent";

const Feedback: React.FC = () => {
  const [height, setHeight] = useState<number>(200);
  const onAdd = () => {
    setHeight(height + 100);
  };
  const onDec = () => {
    setHeight(height - 100);
  };
  const showAlertInfo = () => {
    Sweetalert.alert({
      content: "",
      parentComponent: this,
    });
  };

  return (
    <>
      <ConfigProvider
        value={{
          rowGutter: 20,
          colGutter: 0,
        }}
      >
        <Grid>
          <Alert
            type="info"
            outline
            title=""
            description="Loading"
          />

          <Alert title="AnimateHeight , " />
          <Row>
            <Col span={12}>
              <Button onClick={onAdd}>+100px</Button>
              <Button onClick={onDec}>-100px</Button>
              <AnimateHeight
                height={height}
                style={{ background: "#114db4", marginTop: 16 }}
              >
                <div style={{ height: height }}></div>
              </AnimateHeight>
            </Col>
            <Col span={12}></Col>
          </Row>
          <Alert title="Dialog , openDialog" />
          <Row>
            <Col span={24}>
              <JDialog></JDialog>
            </Col>
          </Row>
          <Alert title="drawer " />
          <Row>
            <Col span={24}>
              <JDrawer></JDrawer>
            </Col>
          </Row>
          <Alert title="loading " />
          <Row>
            <Col span={24}>
              <JLoading></JLoading>
            </Col>
          </Row>
          <Alert title="Placeholder " />
          <Row>
            <Col span={24}>
              {/* @ts-ignore */}
              <Placeholder.RichTextBlock rows={4} />
              {/* @ts-ignore */}
              <Placeholder.RichTextBlock
                rows={7}
                shape="rect"
                size={160}
                dashed={false}
              />
            </Col>
          </Row>
          <Alert title="Sweetalert elementconfirm" />
          <Row>
            <Col span={24}>
              <Button onClick={showAlertInfo}></Button>
            </Col>
          </Row>
        </Grid>
      </ConfigProvider>
    </>
  );
};

export default Feedback;
