import { Dialog, Button } from "zent";
import * as React from "react";
const { openDialog, closeDialog } = Dialog;
const id = "my_dialog";

export default class JDialog extends React.Component {
  state = { visible: false };

  setVisible = (visible) => {
    this.setState({ visible });
  };

  open = () => {
    openDialog({
      dialogId: id, // id is used to close the dialog
      title: " openDialog ",
      children: <div>Hello World</div>,
      footer: <Button onClick={() => closeDialog(id)}></Button>,
      onClose() {
        console.log("outer dialog closed");
      },
    });
  };

  componentDidMount() {
    console.log('componentDidMount');
  }

  shouldComponentUpdate(prevProps: any, nextState: any) {
    console.log(prevProps, nextState, "shouldComponentUpdate");
    return true;
  }

  render() {
    return (
      <div>
        <Button type="primary" onClick={() => this.setVisible(true)}>
           - 
        </Button>
        <Button type="danger" onClick={() => this.open()}>
        openDialog - 
        </Button>
        <Dialog
          visible={this.state.visible}
          onClose={() => this.setVisible(false)}
          footer={<Button onClick={() => this.setVisible(false)}></Button>}
          title=""
        >
          <p>visibleelement</p>
        </Dialog>
      </div>
    );
  }
}
