import {
  Form,
  FormStrategy,
  Checkbox,
  Validators,
  FormNumberInputField,
  FormCheckboxGroupField,
  FormSingleUploadField,
  FormInputField,
  FormUploadField,
  FormImageUploadField,
  Button
} from "zent";
import React, { useCallback } from 'react';

/**
 *  renderError  message
 */
function equalsPassword(value:string, ctx: any):any {
  if (value !== ctx.getSectionValue("password").password) {
    return {
      name: "passwordEqual",
      message: "",
    };
  }
  return null;
}

function idLength(value:any) {
  if (value.length !== 10 && value.length !== 15) {
    return {
      name: "idLength",
      message: "1015",
    };
  }
}

function JJForm() {
  const form = Form.useForm(FormStrategy.View);
  const resetForm = useCallback(() => {
    form.resetValue();
  }, [form]);
  const onSubmit = useCallback((form) => {
    const value = form.getValue();
    console.log(value);
  }, []);
  return (
    <Form form={form} layout="horizontal" scrollToError onSubmit={onSubmit}>
      <FormInputField
        name="name"
        label=":"
        required
        helpDesc=""
        validators={[
          Validators.required(""),
          Validators.pattern(/^[a-zA-Z]+$/, ""),
        ]}
      />
      <FormInputField
        name="password"
        label=":"
        required
        helpDesc=""
        validators={[Validators.required("")]}
        props={{
          type: "password",
        }}
      />
      <FormInputField
        name="confirmPw"
        label=":"
        required
        helpDesc=""
        validators={[equalsPassword]}
        props={{
          type: "password",
        }}
      />
      <FormInputField
        name="email"
        label=":"
        helpDesc=""
        validators={[Validators.email("")]}
      />
      <FormInputField
        name="remark"
        label=":"
        helpDesc=""
        props={{ type: "textarea", showCount: true, maxCharacterCount: 10 }}
        validators={[Validators.maxLength(10, "10")]}
      />
      <FormNumberInputField
        name="id"
        label=":"
        required
        helpDesc=""
        validators={[idLength]}
      />
      <FormCheckboxGroupField
        name="hobbies"
        label=":"
        required
        helpDesc=""
        // @ts-ignore
        validators={[Validators.minLength(2, "")]}
      >
        <Checkbox value="movie"></Checkbox>
        <Checkbox value="book"></Checkbox>
        <Checkbox value="travel"></Checkbox>
      </FormCheckboxGroupField>
      <FormSingleUploadField
        name="singleFile"
        label=":"
        props={{
          tips: " 2M",
          maxSize: 1024 * 1024 * 2,
        }}
        validators={[Validators.required("")]}
      />
      <FormUploadField
        name="upload"
        label=":"
        props={{
          tips: " 2M",
          maxAmount: 9,
          maxSize: 1024 * 1024 * 2,
        }}
        // @ts-ignore
        validators={[Validators.minLength(1, "")]}
      />
      <FormImageUploadField
        name="imageUpload"
        label=":"
        props={{
          tips: " 2M",
          maxAmount: 9,
          maxSize: 1024 * 1024 * 2,
        }}
        // @ts-ignore
        validators={[Validators.minLength(1, "")]}
      />
      <div className="zent-form-actions">
        <Button type="primary" htmlType="submit">
          
        </Button>
        <Button type="primary" outline onClick={resetForm}>
          
        </Button>
      </div>
    </Form>
  );
}

export default JJForm