import React from "react";
import { useHistory } from "react-router-dom";
import "./login.less";
import {
  FormInputField,
  Form,
  FormStrategy,
  Validators,
  Pop,
  Icon,
  Button,
  Notify
} from "zent";
// import imgURL from "../../assets/images/logo.png";

function Login() {
  const form = Form.useForm(FormStrategy.View);
  const [initializing, setInitializing] = React.useState(false);
  const initialize = React.useCallback(() => {
    setInitializing(true);
    setTimeout(() => {
      form.initialize({
        name: "alexwjj",
        password: "88888888",
      });
      setInitializing(false);
    }, 1000);
  }, [form]);
	let history = useHistory();

  const handleClick = () => {
    console.log(form.getValue(),"form");
    const user = form.getValue()
    if(user.name === "alexwjj") {
      sessionStorage.setItem('user', JSON.stringify(user));
      history.push("/home");
    } else {
      Notify.warn('')
    }
    
  };
  return (
    <div className="login">
      <div className="login-logo"></div>
      <div className="login-content">
        {/* <img src={require('../../assets/imgs/fe-logo.png')} alt="logo"  /> */}
        <Form layout="horizontal" form={form} className="login-form">
          <div className="login-form-title">
            {/* <img
              src={imgURL}
              alt="logo"
              style={{ width: "50px", height: "50px" }}
            /> */}
            
          </div>
          <FormInputField
            name="name"
            label={
              <span>
                &nbsp;
                <Pop
                  trigger="hover"
                  content=""
                  centerArrow
                >
                  <Icon type="error-circle-o" />
                </Pop>
                :
              </span>
            }
            validators={[
              Validators.minLength(5, " 5 "),
              Validators.maxLength(25, " 25 "),
            ]}
            helpDesc="alexwjj"
            required=""
          />
          <FormInputField
            name="password"
            props={{ type: "password" }}
            label=":"
            validateOccasion={
              Form.ValidateOccasion.Blur | Form.ValidateOccasion.Change
            }
            validators={[Validators.pattern(/^[0-9]+$/, "")]}
            required=""
          />
          <div style={{ textAlign: "center" }}>
            <Button type="primary" onClick={initialize} loading={initializing}>
              
            </Button>
            <Button type="primary" onClick={handleClick}>
              
            </Button>
            <Button onClick={form.model.clear.bind(form.model)}></Button>
          </div>
        </Form>
      </div>
    </div>
  );
}
export default Login;
