import { Dialog, Button } from "zent";
import React from "react";
import { FormInputField, Form, FormStrategy, Validators } from "zent";
interface IFormValue {
  title: string;
  description: string;
}
interface IProps {
  visible: boolean;
  type: string;
  taskInfo: any;
  onConfirmDialog: (value: IFormValue) => void;
  onCloseDialog(): void;
}

function TaskDialog(props: IProps) {
  const form = Form.useForm(FormStrategy.View);

  setTimeout(() => {
    if (props.type === "detail") {
      form.initialize(props.taskInfo);
    } else {
      form.clear();
    }
  }, 0);

  const resetForm = React.useCallback(() => {
    form.clear();
    form.model.clearError()
    props.onCloseDialog();
  }, [form, props]);
  const onSubmit = React.useCallback(
    (form) => {
      const value = form.getValue();
      props.onConfirmDialog(value);
      form.resetValue();
    },
    [props]
  );

  return (
    <div>
      <Dialog
        visible={props.visible}
        onClose={() => resetForm()}
        title={props.type === "add" ? "" : ""}
      >
        <Form layout="horizontal" form={form} disabled={props.type === "detail"} onSubmit={onSubmit}>
          <FormInputField
            name="title"
            label=":"
            validators={[
              Validators.minLength(2, " 2 "),
              Validators.maxLength(20, " 20 "),
            ]}
            helpDesc="2-20"
            required=""
          />
          <FormInputField
            name="description"
            label=":"
            helpDesc=""
            validateOccasion={
              Form.ValidateOccasion.Blur | Form.ValidateOccasion.Change
            }
            required=""
          />
          {props.type === "add" ? (
            <div className="zent-form-actions">
              <Button type="primary" htmlType="submit">
                
              </Button>
              <Button type="primary" outline onClick={resetForm}>
                
              </Button>
            </div>
          ) : (
            ""
          )}

          {/* <Button htmlType="reset"></Button> */}
        </Form>
      </Dialog>
    </div>
  );
}

export default TaskDialog;
