import "./index.less";
import React, { useState, useEffect } from "react";
import { Alert, Button } from "zent";
import CallbackDemo from "./useCallback";
import ContextDemo from "./useContext";
import ReducerDemo from "./useReducer";
import UseRouter from "./useRouter";
// import useTime from "./useHooks.ts";
// Hooks.defaultProps = {
//   title: 'wjj'
// }

function Hooks(props) {
  const [count, setCount] = useState(0);
  useEffect(() => {
    const titleCache = document.title;
    document.title = `You clicked ${count} times`;

    return () => {
      document.title = titleCache;
    };
  }, [count]);
  // const [time, setTime] = useTime();

  return (
    <div>
      {/* {props.title} */}
      <Alert
        className="mt10"
        title="useCallback/useMemo"
        description="useCallbackuesMemo"
      ></Alert>
      <CallbackDemo></CallbackDemo>
      <Alert
        title="useContext"
        className="mt10"
        description="useContext , "
      ></Alert>
      <ContextDemo></ContextDemo>

      <Alert
        className="mt10"
        title="useEffect"
        description="useEffect,,
      componentDidMountuseEffect(() => { return func },[dep])
      ,returncomponentWillUnmount"
      ></Alert>
      <p>You clicked {count} times</p>
      <Button type="primary" onClick={() => setCount(count + 1)}>
        useEffect
      </Button>

      <Alert
        className="mt10"
        title="useReducer"
        description="useReducer , const [state, dispatch] = useReducer(reducer, initialState);"
      ></Alert>
      <ReducerDemo></ReducerDemo>

      <Alert
        className="mt10"
        title="useHistory"
        description=""
      ></Alert>
      <UseRouter></UseRouter>

      <Alert
        className="mt10"
        title="Hooks"
        description="hooks"
      ></Alert>
      {/* <Button type="primary" onClick={setTime}>
        useTime, 
      </Button>
      <div>{time}</div> */}

    </div>
  );
}


export default Hooks;
