import { Collapse } from "zent";
import * as React from "react";
export default class JCollapse extends React.Component {
  state = {
    activeKey: "1",
  };

  handleChange(activeKey) {
    this.setState({
      activeKey,
    });
  }

  render() {
    const { activeKey } = this.state;
    return (
      <Collapse
        activeKey={activeKey}
        onChange={this.handleChange.bind(this)}
        accordion
      >
        <Collapse.Panel title="" key="1">
          
          
        </Collapse.Panel>
        <Collapse.Panel title="" key="2">
          
          
        </Collapse.Panel>
        <Collapse.Panel title="" key="3">
          
          
        </Collapse.Panel>
      </Collapse>
    );
  }
}
