import React from "react";
import { Button } from "zent";
interface IProps {
  text: string;
  count: number;
}
// 
export default class LifeCycle extends React.Component<IProps, any> {
  constructor(props: any) {
    super(props);
    console.log("constructor");
    // state  constructor 
    this.state = { text: "" };
  }
  // /
  static getDerivedStateFromProps(props: any, state: any) {
    console.log(props, state, "getDerivedStateFromProps");

    return {
      fatherText: props.text,
    };
  }

  // 

  componentDidMount() {
    console.log("componentDidMount");
  }

  // 

  shouldComponentUpdate(prevProps: any, nextState: any) {
    console.log(prevProps, nextState, "shouldComponentUpdate");

    return true;
  }

  // 

  getSnapshotBeforeUpdate(prevProps: any, prevState: any) {
    console.log("getSnapshotBeforeUpdate");

    return "haha";
  }

  // 

  componentDidUpdate(preProps: any, preState: any, valueFromSnapshot: any) {
    console.log("componentDidUpdate");

    console.log(" getSnapshotBeforeUpdate ", valueFromSnapshot);
  }

  // 

  componentWillUnmount() {
    console.log("componentWillUnmount");
  }

  // 

  changeText = () => {
    this.setState({
      text: "",
    });
  };

  render() {
    console.log("render");

    return (
      <div className="container">
        <Button onClick={this.changeText} className="changeText">
          
        </Button>
        
        <p> {this.props.count} </p>

        <p className="textContent">{this.state.text}</p>

        <p className="fatherContent">{this.props.text}</p>
      </div>
    );
  }
}
