import { Card } from 'antd';
import { EChartOption } from 'echarts';
import ReactEcharts from 'echarts-for-react';
import React from 'react';

const Pie = () => {
	const getOptions = (): EChartOption => {
		return {
			backgroundColor: '#2c343c',

			title: {
				text: 'Customized Pie',
				left: 'center',
				top: 20,
				textStyle: {
					color: '#ccc',
				},
			},

			tooltip: {
				trigger: 'item',
			},

			visualMap: [
				{
					show: false,
					min: 80,
					max: 600,
					inRange: {
						colorLightness: [0, 1],
					},
				},
			],
			series: [
				{
					name: '',
					type: 'pie',
					radius: '55%',
					center: ['50%', '50%'],
					data: [
						{ value: 335, name: '' },
						{ value: 310, name: '' },
						{ value: 274, name: '' },
						{ value: 235, name: '' },
						{ value: 400, name: '' },
					].sort(function (a, b) {
						return a.value - b.value;
					}),
					roseType: 'radius',
					label: {
						color: 'rgba(255, 255, 255, 0.3)',
					},
					labelLine: {
						lineStyle: {
							color: 'rgba(255, 255, 255, 0.3)',
						},
						smooth: 0.2,
						length: 10,
						length2: 20,
					},
					itemStyle: {
						color: '#c23531',
						shadowBlur: 200,
						shadowColor: 'rgba(0, 0, 0, 0.5)',
					},

					animationType: 'scale',
					animationEasing: 'elasticOut',
					animationDelay: function () {
						return Math.random() * 200;
					},
				},
			],
		};
	};
  

  const getOptions2 = (): EChartOption => {
		return  {
      title: {
          text: '',
          subtext: '',
          left: 'center'
      },
      tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
      },
      legend: {
          orient: 'vertical',
          left: 'left',
          data: ['', '', '', '', '']
      },
      series: [
          {
              name: '',
              type: 'pie',
              radius: '55%',
              center: ['50%', '60%'],
              data: [
                  {value: 335, name: ''},
                  {value: 310, name: ''},
                  {value: 234, name: ''},
                  {value: 135, name: ''},
                  {value: 1548, name: ''}
              ],
              emphasis: {
                  itemStyle: {
                      shadowBlur: 10,
                      shadowOffsetX: 0,
                      shadowColor: 'rgba(0, 0, 0, 0.5)'
                  }
              }
          }
      ]
  };
  
	};

	return (
		<div>
			<Card title="">
				<ReactEcharts option={getOptions()} style={{ height: 300 }}></ReactEcharts>
				<Card title="">
					<ReactEcharts option={getOptions2()} style={{ height: 300 }}></ReactEcharts>
				</Card>
			</Card>
		</div>
	);
};

export default Pie;
