import "./index.less";
import React, { useState, useEffect, useCallback } from "react";
import { Button, Notify, Tag, Radio } from "zent";

function Child({ onClick }) {
  const [list, setList] = useState<number[]>([]);
  useEffect(() => {
    setList(onClick());
    Notify.info("");
  }, [onClick]);

  return (
    <div>
      {list.map((item, index) => {
        return (
          <div key={index}>
            <Tag>{item}</Tag>;
          </div>
        );
      })}
    </div>
  );
}

function CallbackDemoAdd() {
  const [count, setCount] = useState<number>(0);
  const [value, setValue] = useState<number>(0);
  const [isUseCallback, setIsUse] = useState<boolean>(false);
  const show = () => {
    return [count, count + 1, count + 2];
  };

  const showUse = useCallback(() => {
    return [count, count + 1, count + 2];
  }, [count]);

  const onChange = (e) => {
    setIsUse(e.target.value);
  };

  return (
    <div>
      <div>
        childcountcount
      </div>
      <div>
        <Radio.Group onChange={onChange} value={isUseCallback}>
          <Radio value={false}></Radio>
          <Radio value={true}>useCallback</Radio>
        </Radio.Group>
      </div>

      <Button
        type="primary"
        onClick={() => {
          setCount(count + 1);
        }}
      >
       count+1 {count} 
      </Button>
      <Button
        type="primary"
        onClick={() => {
          setValue(value + 1);
        }}
      >
        value+1 {value}
      </Button>
      <Child onClick={isUseCallback ? showUse : show}></Child>
    </div>
  );
}

export default CallbackDemoAdd;
