/* tslint:disable */
import { Grid, Notify } from "zent";
import React from 'react';

interface IData {
    id: string,
    name: string,
    uv: number,
    stock: number,
}
const columns = [
  {
    title: "gridtable",
    name: "name",
  },
  {
    title: "",
    name: "uv",
  },
  {
    title: "",
    name: "stock",
  },
];

const pageSize = 5;
const totalItem = 10;

const datasets: IData[] = [];
const datasets2: IData[] = [];

for (let i = 0; i < 5; i++) {
  datasets.push({
    id: `f-${i}`,
    name: ` ${i}`,
    uv: 20,
    stock: 5,
  });
  datasets2.push({
    id: `s-${i}`,
    name: ` ${i}`,
    uv: 20,
    stock: 5,
  });
}
interface IState {
    selectedRowKeys: any,
    datasets: IData[],
    current: number
}

export default class Table extends React.Component<any, IState> {
  state = {
    selectedRowKeys: ["f-0"],
    datasets,
    current: 1,
  };
//   @ts-ignore
  onChange = ({ current }) => {
    this.setState({
      current,
      datasets: current === 1 ? datasets : datasets2,
    });
  };

  render() {
    return (
      <Grid
        columns={columns}
        datasets={this.state.datasets}
        pageInfo={{
          pageSize: pageSize,
          total: totalItem,
          current: this.state.current,
        }}
        paginationType="lite"
        selection={{
          selectedRowKeys: this.state.selectedRowKeys,
          onSelect: (selectedRowKeys, selectedRows, currentRow) => {
            if (selectedRowKeys.length > 2) {
              Notify.error("");
              this.setState({
                // @ts-ignore
                selectedRowKeys: [].concat(this.state.selectedRowKeys),
              });
            } else {
              this.setState({
                selectedRowKeys,
              });
            }
          },
          getSelectionProps: (data) => ({
            disabled: data.name === " 1",
            reason: "",
          }),
        }}
        rowKey="id"
        // @ts-ignore
        onChange={this.onChange}
      />
    );
  }
}

