import { Grid } from "zent";
import * as React from "react";
interface IData {
  id: string;
  name: string;
  stock: number;
  type: string;
  company: string;
  phone: string;
  createdTime: string;
  sortBy?: string
  sortType?: number;
}

const datasets: IData[] = [];

for (let i = 0; i < 3; i++) {
  datasets.push({
    id: `id-${i}`,
    name: ` ${i}`,
    type: `type-${i}`,
    company: `company-${i}`,
    phone: `123342345${i}`,
    stock: 5,
    createdTime: "2018-12-11",
  });
}

export default class TableHeaderGroup extends React.Component {
  state = {
    datasets,
    sortBy: '',
    sortType: 0,
  };

  getColumns = () => {
    return [
      {
        title: "",
        name: "name",
        className: "name",
        width: 100,
        fixed: true,
      },
      {
        title: "",
        name: "productInfo",
        children: [
          {
            title: "",
            name: "type",
            width: 200,
          },
          {
            title: "",
            name: "supplier",
            children: [
              {
                title: "",
                name: "company",
                width: 300,
              },
              {
                title: "",
                name: "phone",
                width: 300,
              },
            ],
          },
        ],
      },
      {
        title: "",
        name: "stock",
        defaultText: 0,
      },
      {
        title: "",
        name: "createdTime",
        width: 100,
        needSort: true,
        fixed: "right",
      },
    ];
  };

  onChange = (conf: any) => {
    this.setState({
      ...conf,
    });
  };

  render() {
    const { sortBy, sortType } = this.state;
    return (
      <Grid
        // @ts-ignore
        columns={this.getColumns()}
        datasets={this.state.datasets}
        rowClassName={(data, index) => `${data.id}-${index}`}
        bordered
        scroll={{ x: 1400, y: 400 }}
        sortBy={sortBy}
        // @ts-ignore
        sortType={sortType}
        rowKey="id"
        onChange={this.onChange}
      />
    );
  }
}